.banner-swiper {
  position: relative;
  overflow: hidden;
  border-radius: 0;
  margin-top: 0.46875rem;
  max-height: 26.09375rem;
}
.banner-swiper .carousel {
  overflow: hidden;
}
.banner-swiper .carousel-inner {
  max-height: 26.25rem;
  min-height: 9.375rem;
}
.banner-swiper .carousel-item {
  max-height: 26.25rem;
}
.banner-swiper .swiper-container {
  width: 100%;
  height: 13.125rem;
}
.banner-swiper .swiper-wrapper {
  width: 100%;
  height: 13.125rem;
}
.banner-swiper .swiper-wrapper .adm-swiper-slide {
  width: 100%;
  height: 13.125rem;
  overflow: hidden;
}
.banner-swiper .swiper-wrapper .adm-swiper-indicator .adm-page-indicator-dot {
  width: 1.5625rem;
}
.banner-swiper .carousel {
  position: relative;
  width: 100%;
  max-width: 18.75rem;
  margin: 0 auto;
  overflow: hidden;
}
.banner-swiper .carousel-wrapper {
  display: flex;
  transition: transform 0.5s ease;
  height: 9.375rem;
}
.banner-swiper .carousel-slide {
  min-width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 0.75rem;
}
.banner-swiper .carousel-indicators {
  position: absolute;
  bottom: 0.46875rem;
  left: 0;
  right: 0;
  display: flex;
  justify-content: center;
  gap: 0.25rem;
}
.banner-swiper .indicator {
  width: 0.3125rem;
  height: 0.3125rem;
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0.5);
  cursor: pointer;
  transition: background-color 0.3s;
}
.banner-swiper .indicator.active {
  background-color: white;
}
.banner-swiper .carousel-control {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background-color: rgba(0, 0, 0, 0.3);
  color: white;
  border: none;
  width: 1.25rem;
  height: 1.25rem;
  border-radius: 50%;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.625rem;
}
.banner-swiper .prev {
  left: 0.3125rem;
}
.banner-swiper .next {
  right: 0.3125rem;
}
