.header-warp-server {
  margin: auto;
  background-image: url(https://img.limeetpet.com/limeet/banner2tobg.png);
  background-repeat: no-repeat;
  background-size: 100%;
  position: relative;
  z-index: 9;
  margin-bottom: 0.46875rem;
}
.header-warp-server .bg-body-tertiary {
  background: transparent !important;
}
.header-warp-server .mask {
  height: 12.28125rem;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 11;
  background-color: #D8D8D8;
  opacity: 0.5;
}
.header-warp-server .arrow-left {
  width: 1.78125rem;
  height: 1.78125rem;
  position: absolute;
  z-index: 15;
  top: 0.625rem;
  left: 0.625rem;
  background-image: url('https://img.limeetpet.com/limeet/limeethouseaorrw-left.png');
  background-position: center;
  background-size: 80%;
  background-repeat: no-repeat;
  cursor: pointer;
}
.header-warp-server .main {
  position: absolute;
  z-index: 13;
  top: 4.375rem;
  left: 0;
  display: flex;
}
.header-warp-server .main .des {
  padding-top: 0.46875rem;
  height: 3.125rem;
  color: #000000;
  padding-left: 0.3125rem;
}
.header-warp-server .main .des a {
  color: #333;
}
.header-warp-server .main .des h1 {
  font-size: 1.1875rem;
  font-weight: 450;
  height: 1.25rem;
  line-height: 1.25rem;
  text-shadow: 0 0.03125rem 0.0625rem rgba(51, 51, 51, 0.9);
}
.header-warp-server .main .des h2 {
  font-size: 0.875rem;
  line-height: 0.75rem;
  font-weight: 300;
  text-shadow: 0 0.03125rem 0.0625rem rgba(51, 51, 51, 0.9);
  color: #000;
}
.header-warp-server .main .des h2 img {
  width: 2.1875rem;
  display: inline-block;
  padding-right: 0.3125rem;
}
.header-warp-server .main .des p {
  font-size: 0.8125rem;
  line-height: 0.75rem;
  font-weight: 300;
  text-shadow: 0 0.03125rem 0.03125rem rgba(51, 51, 51, 0.9);
  color: #333;
}
.header-warp-server .main .des img {
  width: 1.125rem;
  display: inline-block;
  padding-right: 0.3125rem;
}
.header-warp-server .main .logo {
  width: 6.25rem;
  height: 6.25rem;
  padding: 0.15625rem;
  margin-left: 0.625rem;
}
.header-warp-server .main .logo img {
  width: 6.25rem;
  height: 6.25rem;
}
.header-warp-server .main:hover {
  opacity: 0.9;
}
.header-warp-server .header {
  width: 100%;
  height: 10.96875rem;
}
.header-warp-server .nav-sub {
  width: 100%;
  position: relative;
  z-index: 99;
  font-weight: 500;
}
.header-warp-server .nav-sub .mask-sub {
  position: absolute;
  width: 100%;
  height: 3.5625rem;
  background-color: #D8D8D8;
  opacity: 0.7;
  left: 0;
  top: 0;
  overflow: hidden;
}
.header-warp-server .nav-sub ul {
  height: 3.5625rem;
  padding: 0;
  margin: 0;
  display: flex;
}
.header-warp-server .nav-sub ul li {
  height: 3.5625rem;
  line-height: 3.5625rem;
  text-align: center;
  font-size: 0.5rem;
  border-bottom: 0.0625rem solid #D8D8D8;
  cursor: pointer;
  position: relative;
}
.header-warp-server .nav-sub ul li i.triangle {
  width: 0;
  height: 0;
  border-left: 0.3125rem solid transparent;
  border-right: 0.3125rem solid transparent;
  border-bottom: 0.3125rem solid #666;
  transition: all 0.3s ease;
  display: inline-block;
  overflow: hidden;
  margin-bottom: 0.15625rem;
}
.header-warp-server .nav-sub ul li.active {
  border-bottom: 0.0625rem solid #F6912D;
}
.header-warp-server .nav-sub ul li.active i.triangle {
  border-bottom-color: #F6912D;
}
.header-warp-server .nav-sub ul li:hover .dropdown-wrap {
  display: block;
}
.header-warp-server .nav-sub ul li.active a {
  color: #F6912D;
  font-weight: bold;
}
.header-warp-server .nav-sub ul .dropdown-wrap {
  padding: 0.3125rem;
  border: 0.03125rem solid #d8d8d8;
  background: #fff;
  border-radius: 0.3125rem;
  width: 15rem;
  opacity: 0.9;
  display: none;
  position: absolute;
  z-index: 109;
  left: 0.3125rem;
  top: 3.125rem;
}
.header-warp-server .nav-sub ul .dropdown-wrap i.triangle-top {
  width: 0.9375rem;
  height: 0.75rem;
  position: absolute;
  left: 0.9375rem;
  top: -0.75rem;
  margin: 0;
  padding: 0;
  overflow: hidden;
  display: inline-block;
  background-image: url('/triangle.png');
  background-repeat: no-repeat;
  background-size: 100%;
  background-position: center;
}
.header-warp-server .nav-sub ul .dropdown-wrap span {
  display: block;
  padding: 0;
  margin: 0;
}
.header-warp-server .nav-sub ul .dropdown-wrap span.active {
  background-color: #fff5e2;
}
.header-warp-server .nav-sub ul .dropdown-wrap a.nav-link {
  padding-right: 0.3125rem;
  padding-left: 0.3125rem;
  cursor: pointer;
  font-weight: 400;
  letter-spacing: 0.0625rem;
  font-size: 1rem;
  color: #333;
  text-align: left;
  height: 1.875rem;
  line-height: 1.875rem;
}
.header-warp-server .nav-sub ul .dropdown-wrap a:hover {
  color: #F6912D;
}
.header-warp-server .nav-sub ul .dropdown-wrap a.active {
  color: #F6912D;
  font-weight: bold;
}
.header-warp-server .nav-sub ul .dropdown-wrap:hover {
  display: block;
}
.header-warp-server .navbar {
  padding: 0 0 0 0.9375rem;
}
.header-warp-server .navbar a.nav-link {
  padding-right: 0.78125rem;
  padding-left: 0.78125rem;
  cursor: pointer;
  font-weight: bold;
  letter-spacing: 0.0625rem;
  font-size: 1rem;
  color: #333;
}
.header-warp-server .navbar a:hover {
  color: #F6912D;
}
.header-warp-server .navbar a:hover i.triangle {
  border-bottom: 0;
  border-top: 0.3125rem solid #F6912D;
}
.header-warp-server .navbar a.active {
  color: #F6912D;
  font-weight: bold;
}
.header-warp-server .navbar .dropdown-item:active {
  background-color: #D8D8D8;
}
